.BSuper {
  background-image: url("../../Images/fondobenefits.png");
  /* background-size: cover; */
  background-repeat: repeat;
  background-attachment: fixed;
  background-position: center;
  width: 100%;
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 0rem;
  /* padding-bottom: 2rem; */
  margin: 0%;
  background-color: #a95bb3;
  border-top: solid black 7px;
  border-bottom: solid black 7px;
}

.BTitle {
  height: 6rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0%;
  background-color: #8cb00c;
  border: solid black 7px;
  border-radius: 2rem;
  padding: 0rem 10px;
  /* padding-bottom: 0.5rem; */
}

.BTitle p {
  font-size: 30px;
  /* margin-bottom: -20px; */
  /* line-height: 0px; */
  margin: 0px;
  padding: 0px 0px;
  /* border: solid red 1px; */
}

.BTitle h1 {
  color: rgb(84, 14, 114);
  font-size: 40px;
  padding: 0px 0px;
  /* line-height: 0px; */
  margin: 0;
  /* border: solid black 1px; */
}

.BCardBox {
  display: flex;
  flex-direction: row;
  /* grid-auto-flow: row; */
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 70%;
  margin-top: 50px;

  /* border: #8cb00c solid 1px; */
}

.BCard {
  position: relative;
  max-width: 25%;
  height: 230px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0%;
  background-color: orange;
  border: solid black 7px;
  border-radius: 2rem;
  padding: 1rem 1rem;
  padding-top: 2rem;
  margin: 15px;
  font-size: 26px;
  transition: 0.5s ease-in-out;
}

.BCard:hover {
  transform: translateY(-10px);
  cursor: default;
}

.BCardTitle {
  position: absolute;
  width: 50%;
  height: 30px;
  /* height: 700px; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0%;
  background-color: yellow;
  border: solid black 7px;
  border-radius: 2rem;
  padding: 1rem 1rem;
  top: -40px;
  font-size: 15px;
  line-height: 25px;
}

@media screen and (max-width: 1250px) {
  .BCard {
    max-width: 40%;
  }
}

@media screen and (max-width: 1024px) {
  .BCard {
    max-width: 90%;
  }

  .BTitle p {
    font-size: 20px;
  }

  .BTitle h1 {
    font-size: 25px;
  }
}

@media screen and (max-width: 500px) {
  .BCardBox {
    width: 90%;
  }

  .BCardTitle {
    font-size: 14px;
  }
}
