.CSuper {
  width: 100%;
  position: relative;
  background-color: black;
  /* height: 700px; */
  /* display: flex; */
  padding: 0rem 0rem;
  margin: 0%;
  /* background-color: #8cb00c; */
  /* border-top: solid black 7px; */
  /* border-bottom: solid black 7px; */
  z-index: 10;
}

.CSwiper {
  cursor: grab;
  z-index: 50;
}

.CTitle {
  z-index: 100;
  position: absolute;
  left: 45%;
  width: 10%;
  height: 0px;
  /* height: 700px; */
  display: flex;
  /* justify-self: center; */
  justify-content: center;
  align-items: center;
  /* margin: auto; */
  background-color: yellow;
  border: solid black 7px;
  border-radius: 2rem;
  padding: 2rem 3.5rem;
  top: -40px;

  font-size: 24px;
  color: rgb(84, 14, 114);
}

@media screen and (max-width: 1100px) {
  .CTitle {
    left: 25%;
  }
}
