.FSuper {
  width: 100%;
  /* height: 300px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem 0rem;
  /* padding-bottom: 2rem; */
  margin: 0%;
  background-color: rgb(84, 14, 114);
  /* border-top: solid black 7px; */
  border-bottom: solid black 7px;
  font-size: smaller;
  z-index: 50;
}

.FBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.FSocials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 160px;
  padding: 25px 10px;

  /* border: solid white 1px; */
}

@media screen and (max-width: 1024px) {
  .FSuper {
    font-size: 8px;
  }
}
