.HSuper {
  background-image: url("../../Images/heroraw.png");
  background-size: cover;
  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  background-position: center;
  display: flex;
  width: 100%;
  /* border: solid red 1px; */
  height: 800px;
}

@media screen and (max-width: 1024px) {
  .HSuper {
    background-image: url("../../Images/herosmall.png");
    /* height: 500px; */
    background-size: 1200px;
    height: 700px;
  }
}

@media screen and (max-width: 800px) {
  .HSuper {
    background-image: url("../../Images/herosmaller.png");
    /* height: 500px; */
    background-size: 1200px;
    height: 800px;
  }
}

.HBox {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.CTAbtn {
  width: 180px;
  height: 80px;
  border: solid #000000 4px;
  font-family: "Patrick Hand", cursive;
  font-weight: bolder;
  color: rgb(246, 255, 0);
  background-color: rgb(255, 0, 0);
  border-radius: 3rem;
  box-shadow: inset 0 0 0 0 rgb(242, 184, 49);
  transition: ease-out 0.3s;
  font-size: 50px;
  outline: none;
  cursor: pointer;
  animation: infinite 1s ease-in-out;
  animation-name: btnclickme;
}

.CTAbtn:hover {
  /* color: rgb(30, 112, 35); */
  background-color: rgb(55, 255, 0);
  color: rgb(84, 14, 114);
  /* border: solid rgb(45, 255, 90) 4px; */
}

@keyframes btnclickme {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
