.RRSuper {
  background-image: url("../../Images/fondoabout.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  width: 100%;
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: baseline;
  padding: 2rem 0rem;
  min-height: 50vh;
  /* padding-bottom: 4rem; */
  margin: 0%;
  background-color: #8cb00c;
  border-top: solid black 7px;
  border-bottom: solid black 7px;
}

.MainTitle {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px 0px;
}

.MainTitle h1 {
  font-size: 70px;
  margin: 0;
  animation: btnclickme 4s infinite ease-in-out;
}

.MainTitle p {
  font-size: 40px;
  margin: 0;
}

.MainTitle span {
  font-size: 40px;
  margin: 0;
  color: rgb(84, 14, 114);
}

.MainFilter {
  width: 70%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.RRFilterBtn {
  height: 10px;
  background-color: orange;
  border-radius: 10px;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 16px 10px;
  transition: ease-out 0.3s;
  margin: 10px;
  border: 4px solid #000000;
}

.RRFilterBtn p {
  text-decoration: none;
  font-size: 28px;
  color: rgb(84, 14, 114);
}

.RRFilterBtn:hover {
  /* border: 4px solid rgb(90, 90, 90); */
  transform: translateY(-5px);
}

.HL {
  background-color: rgb(84, 14, 114);
  color: orange;
}

.HL p {
  text-decoration: none;
  font-size: 28px;
  color: orange;
}

.CardContainer {
  width: 100%;
}

.RRContainerCommon,
.RRContainerRare,
.RRContainerEpic {
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: black solid 7px;
  transition: 0.3s ease-in-out;
  padding-bottom: 40px;

  /* border: red solid 2px; */
}

.RRContainerCommon {
  background-image: url("../../Images/fondorarity2.png");
}

.RRContainerRare {
  background-image: url("../../Images/fondorarity1.png");
}

.RRContainerEpic {
  background-image: url("../../Images/fondorarity3.png");
  border-bottom: black solid 7px;
}

.Collections {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  /* border: red solid 2px; */
}

.SectionTitle {
  height: 30px;
  width: 160px;
  border-radius: 2rem;
  border: 4px solid #000000;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
  font-family: "Patrick Hand", cursive;
  font-size: 20px;
  padding: 16px 10px;
  text-decoration: none;
  transition: ease-out 0.3s;
  margin: 20px;
  animation: happy 1s infinite linear;
}

@keyframes happy {
  25% {
    rotate: 3deg;
  }
  75% {
    rotate: -3deg;
  }
}

.Com {
  background-color: grey;
}

.Rar {
  background-color: rgb(244, 134, 49);
}

.Epi {
  background-color: rgb(216, 37, 222);
}

.Card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: yellow;
  height: 300px;
  width: 220px;
  border: black solid 7px;
  border-radius: 1rem;
  margin: 10px;
  padding: 10px 0px;
  transition: 0.3s ease-in-out;
}

.Card:hover {
  transform: translateY(-5px) scale(1.05);
}

.Card img {
  border: black solid 7px;
  height: 200px;
  background-color: rgb(255, 0, 0);
}

.Card h1 {
  font-size: 25px;
  margin: 0;
}

/*=========Media Queries=========*/

@media screen and (max-width: 700px) {
  /* .MainTitle {} */

  .MainTitle h1 {
    font-size: 40px;
  }

  .MainTitle p {
    font-size: 22px;
  }

  .MainTitle span {
    font-size: 22px;
  }
}
