.ASuper {
  background-image: url("../../Images/fondoabout.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  width: 100%;
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 7.5rem 0rem;
  /* padding-bottom: 4rem; */
  margin: 0%;
  background-color: #8cb00c;
  border-top: solid black 7px;
  border-bottom: solid black 7px;
}

.ABox {
  position: relative;
  width: 50%;
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0px;
  background-color: orange;
  border: solid black 7px;
  border-radius: 2rem;
  padding: 4rem 4rem;
}

.ABox h1 {
  font-size: 36px;
  color: rgb(84, 14, 114);
}

.ABox img {
  float: left;
  height: 150px;
  padding: 0.7rem;
}

.ABox p {
  font-size: 24px;
}

.ATitleBox {
  position: absolute;
  width: 30%;
  height: 0px;
  /* height: 700px; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0%;
  background-color: yellow;
  border: solid black 7px;
  border-radius: 2rem;
  padding: 2rem 4rem;
  top: -40px;
  line-height: 30px;
}

.ADecoBox {
  position: absolute;
  bottom: -80px;
  left: -100px;
  width: 120%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;

  /* border:height="100"  solid yellow 2px; */
}

/*===============Media Queries===============*/

@media screen and (max-width: 780px) {
  .ATitleBox {
    height: 40px;
    top: -60px;
  }

  .ABox {
    /* width: 100%; */
    padding: 4rem 4rem;
  }

  .ABox p {
    font-size: 20px;
    text-align: justify;
    padding: 0px;
    margin: 0px;
  }

  .ABox img {
    float: none;
  }

  .ABoxContent {
    width: 130%;
  }

  .ADecoBox {
    bottom: -60px;
    left: 4%;
    width: 90%;
  }

  .ADecoBox img {
    height: 80px;
  }
}
