.RSuper {
  background-image: url("../../Images/fondoroadmap.png");
  /* background-size: cover; */
  background-repeat: repeat;
  background-attachment: fixed;
  background-position: center;
  width: 100%;
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 6rem 0rem;
  /* padding-bottom: 4rem; */
  margin: 0%;
  background-color: #fbe859;
  /* border-top: solid black 7px; */
  border-bottom: solid black 7px;
}

.RBox {
  position: relative;
  width: 60%;
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0%;
  background-color: orange;
  border: solid black 7px;
  border-radius: 2rem;
  padding: 4rem 0rem;
}

.RTitle {
  position: absolute;
  width: 40%;
  height: 40px;
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0%;
  background-color: yellow;
  border: solid black 7px;
  border-radius: 2rem;
  padding: 2rem 2rem;
  top: -40px;
}

.RTitle p {
  font-size: 25px;
  margin: 0;
  margin-bottom: -15px;
  padding: 0 0;
  line-height: 20px;
}

.RTitle h1 {
  color: rgb(84, 14, 114);
  font-size: 50px;
  margin: 0;
  padding: 0 0;
}

.RContent {
  display: flex;
  flex-direction: row;
}

.RImg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  padding: 0 40px;

  /* border: solid red 1px; */
}

.RText {
  display: flex;
  align-items: center;
  width: 50%;
  padding: 0 40px;
  font-size: 20px;
  text-align: left;

  /* border: solid white 1px; */
}

@media screen and (max-width: 1024px) {
  .RTitle {
    top: -60px;
  }

  .RTitle p {
    font-size: 20px;
  }

  .RTitle h1 {
    font-size: 30px;
    line-height: 60px;
  }

  .RBox {
    width: 90%;
  }

  .RContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  /* .RImg {
    width: 90%;
  } */

  .RText {
    width: 80%;
  }

  .RImg img {
    height: 200px;
  }
}
