.USuper {
  background-image: url("../../Images/fondoutils.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  width: 100%;
  min-height: 85vh;
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem 0rem;
  /* padding-bottom: 4rem; */
  margin: 0%;
  background-color: #8cb00c;
  /* border-top: solid black 7px; */
  border-bottom: solid black 7px;
  overflow: hidden;
}

.USidePanel {
  position: fixed;
  left: 0;
  top: 0px;
  display: flex;
  flex-direction: row;
  /* justify-content: space-between; */
  /* align-items: center; */
  /* width: 200px; */
  height: 100vh;
  /* border-right: solid black 7px; */
  /* background-color: rgb(84, 14, 114); */
}

.UMainPanel {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  height: 99vh;
  border-right: solid black 7px;
  background-color: rgb(84, 14, 114);
  border-bottom: solid black 7px;
  transition: 0.3s ease-in-out;
}

.UBtnContainer {
  margin-top: 70px;
  width: 100%;
}

.UPanelBtn {
  display: flex;
  flex-direction: row;
  border-top: 3px solid black;
  border-bottom: 3px solid black;
  width: 100%;
  justify-content: baseline;
  align-items: center;
  cursor: pointer;
  padding: 10px 0px;
  color: orange;
  transition: 0.3s ease-in-out;
}

.UHL {
  background-color: orange;
}

.UHL p {
  color: rgb(84, 14, 114);
}

.UPanelBtn img {
  height: 40px;
  margin-left: 20px;
  margin-right: 20px;
}

.UPanelBtn p {
  font-size: 26px;
  margin: 0;
}

.UPanelBtn:hover {
  background-color: rgb(136, 25, 183);
}

.UHL:hover {
  background-color: orange;
}

.UBox {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.UBox h1 {
  text-align: center;
  font-size: 15px;
  margin-bottom: 20px;
}

.UBox img {
  height: 100px;
}

.UFSuper {
  width: 100%;
  /* height: 300px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem 0rem;
  /* padding-bottom: 2rem; */
  margin: 0%;
  background-color: rgb(84, 14, 114);
  /* border-top: solid black 7px; */

  font-size: smaller;
  z-index: 50;
}

.USocials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 140px;
  padding: 25px 10px;

  /* border: solid white 1px; */
}

.USocials img {
  height: 40px;
}

@media screen and (max-width: 1024px) {
  .UBox img {
    height: 70px;
  }

  .USocials {
    padding: 10px 0px;
  }

  .USocials img {
    height: 40px;
  }
}

/*=========Responsive Button=========*/
@media screen and (max-width: 1024px) {
  .UDisplayPanel {
    width: 30px;
    border-bottom: solid black 7px;
    border-right: solid black 7px;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(136, 25, 183);
  }
}
.UMainAlt {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 40px;
  height: 99vh;
  border-right: solid black 7px;
  background-color: rgb(84, 14, 114);
  border-bottom: solid black 7px;
  transition: 0.3s ease-in-out;
}

@media screen and (max-width: 1024px) {
  .UPanelBtn img {
    margin-left: 0px;
    margin-right: 0px;
  }
}
/*=========Main Content=========*/

.UContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: baseline;
  width: 100%;

  /* border: solid 2px blue; */
}

.UContent img {
  height: 300px;
}

.Sneakpeek h1 {
  text-align: center;
}

.Sneakpeek img {
  height: 600px;
}

@media screen and (max-width: 1024px) {
  .UContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: baseline;
    width: 100%;

    margin-left: 100px;

    /* border: solid 2px blue; */
  }

  .UContent img {
    height: 200px;
  }

  .UContent h2 {
    font-size: 20px;
    text-align: right;
  }

  .Sneakpeek h1 {
    font-size: 20px;
    text-align: right;
  }

  .Sneakpeek img {
    height: auto;
    width: 90%;
  }
}
