.NavSuper {
  display: flex;
  flex-direction: row;
  height: 70px;
  width: 100%;
  justify-content: center;
  background-color: rgb(84, 14, 114);
  font-size: 1rem;
  color: orange;
  text-decoration: none;
  border-top: solid black 5px;
  border-bottom: solid black 5px;
  z-index: 20;

  position: sticky;
  top: 0;

  /* border: solid 1px red; */
}

.NavContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
}

.Navbox {
  display: flex;
  flex-direction: row;
  align-items: center;

  /* border: black solid 1px; */
}

.Navlogo {
  display: flex;
  align-items: center;
  /* width: 4.5%; */
  /* padding-left: 200px; */
  min-width: 50px;
  width: 10%;

  /* border: rgb(203, 143, 41) solid 1px; */
}

.Navlogo img {
  height: 100%;
  cursor: pointer;
}

.NavlogoAlt {
  display: flex;
  align-items: center;
  /* width: 4.5%; */
  /* padding-left: 200px; */
  min-width: 50px;
  width: 10%;

  /* border: rgb(203, 143, 41) solid 1px; */
}

.NavlogoAlt img {
  height: 100%;
  cursor: pointer;
}

.NavRightPanel {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 70%;
  min-width: 385px;

  /* border: rgb(175, 180, 39) solid 3px; */
}

.Navegacion {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 35%;
  min-width: 550px;
  /* padding: 0rem 15px; */

  /* border: rgb(26, 117, 88) solid 1px; */
}

.Navegacion h1 {
  padding: 0px 10px;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.Navegacion h1:hover {
  cursor: pointer;
  color: #7cff3a;
}

.Desplegable {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0rem 1rem;
  /* width: 8%; */
  min-width: 95px;

  /* border: rgb(78, 62, 185) solid 1px; */
}

.MenuBtn {
  height: 40px;
  background-color: orange;
  border-radius: 10px;
  border: 4px solid #000000;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  color: rgb(84, 14, 114);
  font-family: "Patrick Hand", cursive;
  font-size: 28px;
  padding: 16px 10px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #2f6627;
  transition: ease-out 0.3s;
}
.MenuBtn:hover {
  background-color: #7cff3a;
}
.MenuBtn:active {
  position: relative;
  top: 1px;
}

.Social {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0rem 7px;
  /* width: 10%; */
  min-width: 140px;

  /* border: rgb(48, 187, 45) solid 1px; */
}

.Navmint {
  display: flex;
  align-items: center;
  justify-content: center;
  /* padding: 0rem 1rem; */
  height: 100%;
  /* width: 8%; */
  min-width: 110px;

  /* border: rgb(176, 51, 51) solid 1px; */
}

.NavbtnCTA {
  width: 110px;
  height: 55px;
  border: solid #000000 4px;
  font-family: "Patrick Hand", cursive;
  font-weight: bolder;
  color: rgb(84, 14, 114);
  background-color: rgb(0, 255, 72);
  border-radius: 2rem;
  box-shadow: inset 0 0 0 0 rgb(242, 184, 49);
  transition: ease-out 0.3s;
  font-size: 30px;
  outline: none;
  cursor: pointer;
  animation: infinite 1s ease-in-out;
  animation-name: btnclickme;
}

@keyframes btnclickme {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.NavbtnCTA:hover {
  /* color: rgb(30, 112, 35); */
  background-color: yellow;
  /* border: solid rgb(45, 255, 90) 4px; */
}

.NSideMenu {
  width: 0%;
  min-width: 0px;
  pointer-events: none;
  opacity: 0;
}

.NSideMenu img {
  width: 0px;
}

/*============Media Queries============*/

@media screen and (max-width: 1150px) {
  .NavContainer {
    justify-content: space-around;
  }

  .NavRightPanel {
    justify-content: right;
    min-width: 0px;
  }

  .Navegacion {
    width: 0%;
    min-width: 0px;
    pointer-events: none;
    opacity: 0;
  }

  .Navegacion h1 {
    padding: 0px px;
    cursor: none;
    transition: 0.2s ease-in-out;
    font-size: 1px;
  }

  .Social {
    width: 0%;
    min-width: 0px;
    pointer-events: none;
    opacity: 0;
    padding: 0px 0px;

    border: rgb(48, 187, 45) solid 1px;
  }

  .Social img {
    width: 1px;
  }

  .Navmint {
    width: 0%;
    min-width: 0px;
    pointer-events: none;
    opacity: 0;
  }

  .NavbtnCTA {
    width: 0px;
    font-size: 1px;
  }

  .NSideMenu {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20%;
    min-width: 50px;
    pointer-events: auto;
    opacity: 1;
  }

  .sideMenuBtn {
    background: transparent;
    border: none;
    /* border: #7cff3a solid 1px; */
  }

  .NSideMenu img {
    width: 50px;
  }
}

@media screen and (max-width: 424px) {
  .Navlogo {
    width: 0px;

    /* border: rgb(203, 143, 41) solid 1px; */
  }

  .Navlogo img {
    height: 0px;
  }
}

.NPaneles {
  position: absolute;
  height: 500px;
  top: 64px;
  width: 98%;
  margin: 0%;
  padding: 0% 0%;
  display: flex;
  justify-content: center;
  /* align-items: center; */
  pointer-events: none;

  /* border: #7cff3a solid 3px; */
}

/*============Pop Window============*/

.DDPanel-closed {
  position: absolute;
  height: 32%;
  width: 250px;
  border: solid black 7px;
  border-radius: 2rem;
  background-color: orange;
  /* top: 30px; */
  /* right: -100%; */
  pointer-events: none;
  transition: 0.2s ease-in-out;
  opacity: 0;
  display: flex;
  padding: 30px 30px;
  z-index: 10;
}

.DDPanel-open {
  position: absolute;
  height: 300px;
  width: 250px;
  border: solid black 7px;
  border-radius: 2rem;
  background-color: orange;
  /* top: 0px; */
  /* margin-top: 68px; */
  pointer-events: auto;
  transition: 0.3s ease-in-out;
  opacity: 1;
  display: flex;
  padding: 30px 30px;
  z-index: 10;
}

.InnerPanel {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.PanelBtn {
  height: 60px;
  width: 250px;
  background-color: orange;
  border-radius: 2rem;
  border: 4px solid #000000;
  display: inline-flex;
  justify-content: left;
  align-items: center;
  cursor: pointer;
  color: rgb(84, 14, 114);
  font-family: "Patrick Hand", cursive;
  font-size: 24px;
  padding: 16px 5px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #2f6627;
  transition: ease-out 0.3s;
  margin: 5px;
}
.PanelBtn:hover {
  background-color: yellow;
}
.PanelBtn:active {
  position: relative;
  top: 1px;
}

/*============Side Menu Window============*/

